// About Us page icons


// AboutUsBlock icons
export const IconsTraktor = () => {
    return (
        <svg width="80" height="48" viewBox="0 0 52 48" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
            <ellipse cx="10.1464" cy="9.5" rx="10.0278" ry="9.5" fill="#FFF4CD"/>
            <rect x="5.11865" y="2" width="46" height="46" fill="url(#pattern0_2_14094)"/>
            <defs>
                <pattern id="pattern0_2_14094" patternContentUnits="objectBoundingBox" width="1" height="1">
                    <use xlinkHref="#image0_2_14094" transform="scale(0.01)"/>
                </pattern>
                <image id="image0_2_14094" width="100" height="100" xlinkHref="data:image/png;base64,"/>
            </defs>
        </svg>
    );
};

